.application {
  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &--title {
      font-family: $main-font-family;
      font-size: 44px;
      font-weight: 600;
      line-height: 52.8px;
      text-align: center;
      color: #253757;
      margin-block: 0 24px;
    }

    &--text {
      font-family: $text-font-family;
      font-size: 22px;
      font-weight: 600;
      line-height: 33px;
      text-align: center;
      color: #6c788b;
      margin: 0;
    }
  }

  &__button {
    height: 50px;
    background-color: $main-blue-color;
    border: 1px solid $main-blue-color;
    border-radius: 8px;
    width: 280px;
    box-sizing: border-box;
    margin-block: 56px 72px;
    cursor: pointer;

    &:hover {
      @include hover(transform, scale(1.1));
    }

    &--text {
      font-family: $main-font-family;
      font-size: 22px;
      font-weight: 600;
      line-height: 33px;
      text-align: center;
      color: $white-color;
      text-decoration: none;
    }
  }
}
