.send__msg {
  grid-column: 1 / 3;

  @include on-tablet {
    grid-column: 1 / 5;
  }

  @include on-small-desktop {
    grid-column: 1 / 6;
  }

  &--title {
    font-family: $main-font-family;
    font-size: 32px;
    font-weight: 600;
    line-height: 41.6px;
    text-align: left;
    color: $white-color;
    margin-block: 72px 48px;

    @include on-tablet {
      margin-block: 180px 56px;
    }
  }
}

.message__form {
  background-color: transparent;
  font-family: $main-font-family;
  font-size: 15px;
  font-weight: 600;
  line-height: 22.5px;
  text-align: left;
  color: $white-color;
  border: none 1px;
  border-bottom: 1px solid $white-color;
  display: block;
  margin-bottom: 40px;
  width: 100%;
  padding-block: 0 17px;

  &:focus {
    outline: none;
    border-bottom: 1px solid $main-blue-color;
  }

  &--button {
    height: 50px;
    background-color: $main-blue-color;
    border: 1px solid $main-blue-color;
    border-radius: 8px;
    width: 280px;
    box-sizing: border-box;
    margin-block: 56px 80px;
    cursor: pointer;
    margin-bottom: 0;

    &:hover {
      @include hover(transform, scale(1.1));
    }

    &--text {
      font-family: $main-font-family;
      font-size: 15px;
      font-weight: 600;
      line-height: 22.5px;
      text-align: center;
      color: $white-color;
      text-decoration: none;
    }
  }
}
